	module.exports = {"data":{"markdownRemark":{"html":"<blockquote>\n<p>\"I am an old man and I have known a great many troubles, but most of them\nnever happened.\" by Mark Twain</p>\n</blockquote>\n<p>As software developers we have a lot of things that build unnecessary stress\nand anxiety in each day of our profession - just to mention a few and in no\nparticular order:</p>\n<ul>\n<li>Collaborating with different types of personalities</li>\n<li>Dealing with conflict</li>\n<li>Estimating effort</li>\n<li>Managing expectations</li>\n<li>Choosing the right technologies</li>\n<li>Working on legacy repositories</li>\n<li>Fighting fires in production</li>\n</ul>\n<p>In many cases we don't know what the outcome of a situation will be. Although\nthis applies to all aspects of life, dealing with the unknown only amplifies\nthe stresses that build.</p>\n<p>A mentor introduced me to these three questions I could ask myself (and write\ndown the answers to) when I find myself in a stressful situation\nwhere I can't predict the outcome:</p>\n<ol>\n<li>What do I think is the worst possible realistic outcome of the situation?</li>\n<li>How will I feel about it?</li>\n<li>What will I do about it?</li>\n</ol>\n<p>I've seen the value of applying these questions both in my professional and\npersonal capacity.</p>\n<p>When I explore the absolute worst realistic outcome I can think of, I've\ngiven the situation deliberate thought and processed a way to deal with how I\nfeel about it and how I can overcome it.</p>\n<blockquote>\n<p>I am an old man and I have known a great many troubles, but most of them\nnever happened. ~ Mark Twain</p>\n</blockquote>\n<p>It's actually rare that the worst outcome will materialize so just by thinking\nabout it I am equipped to take on whatever comes my way without getting worked\nup about it.</p>","timeToRead":1,"excerpt":"\"I am an old man and I have known a great many troubles, but most of them\nnever happened.\" by Mark Twain As software developers we have a…","frontmatter":{"title":"Coping with unknown outcomes","cover":"https://picsum.photos/1600/800/?image=639","date":"2016-08-01 20:54:23 +0200","category":null,"tags":["Mindset"]},"fields":{"slug":"/blog/coping-with-unknown-outcomes"}},"prev":{"excerpt":"A fun way to practice and familiarize yourself with development tools,\nworkflows, patterns and practices. Wikipedia \nstates that a code kata is an  exercise in programming  which helps\na programmer…","timeToRead":3,"frontmatter":{"title":"Code katas","cover":"https://picsum.photos/1600/800/?image=532","date":"2016-08-13 19:44:42 +0200"},"fields":{"slug":"/blog/code-katas"}},"next":{"excerpt":"Everything I did today was done with intent and slower than usual. I was\nmindful. Today I had complete control of my day and my emotions because I was mindful. I walked mindfully. I spoke mindfully. I…","timeToRead":3,"frontmatter":{"title":"A mindful day of happiness","cover":"https://picsum.photos/1600/800/?image=646","date":"2016-07-22 21:54:15 +0200"},"fields":{"slug":"/blog/a-mindful-day-of-happiness"}}},"pathContext":{"slug":"/blog/coping-with-unknown-outcomes","total":57,"prev":"/blog/code-katas","next":"/blog/a-mindful-day-of-happiness"}}

